@keyframes loading1 {
    0% {
        transform: rotate(0deg); }

    50% {
        transform: rotate(-45deg); }

    100% {
        transform: rotate(0deg); } }

@keyframes loading2 {
    0% {
        transform: rotate(180deg); }

    50% {
        transform: rotate(225deg); }

    100% {
        transform: rotate(180deg); } }

.loading-object > div:nth-child(2) {
    transform: translate(-15px, 0); }

.loading-object > div:nth-child(2) div {
    position: absolute;
    top: 40px;
    left: 40px;
    width: 120px;
    height: 60px;
    border-radius: 120px 120px 0 0;
    background: $yellow;
    animation: loading1 .5s linear infinite;
    transform-origin: 60px 60px; }

.loading-object > div:nth-child(2) div:nth-child(2) {
    animation: loading2 .5s linear infinite; }

.loading-object > div:nth-child(2) div:nth-child(3) {
    transform: rotate(-90deg);
    animation: none; }

@keyframes loading-object-3 {
    0% {
        transform: translate(190px, 0);
        opacity: 0; }

    20% {
        opacity: 1; }

    100% {
        transform: translate(70px, 0);
        opacity: 1; } }

.loading-object > div:nth-child(1) {
    display: block; }

.loading-object > div:nth-child(1) div {
    position: absolute;
    top: 92px;
    left: -8px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: darken($white, 50%);
    animation: loading-object-3 1s linear infinite; }

.loading-object > div:nth-child(1) div:nth-child(1) {
    animation-delay: -0.67s; }

.loading-object > div:nth-child(1) div:nth-child(2) {
    animation-delay: -0.33s; }

.loading-object > div:nth-child(1) div:nth-child(3) {
    animation-delay: 0s; }

.loading-spinner {
    width: 200px;
    height: 200px;
    display: inline-block;
    overflow: hidden; }

.loading-object {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0 /* see note above */; }

.loading-object div {
    box-sizing: content-box; }

// LOADING 2
@keyframes ldio {
    0% {
        top: 94px;
        left: 94px;
        width: 0;
        height: 0;
        opacity: 1; }

    100% {
        top: 49px;
        left: 49px;
        width: 90px;
        height: 90px;
        opacity: 0; } }

.ldio div {
    position: absolute;
    border-width: 6px;
    border-style: solid;
    opacity: 1;
    border-radius: 50%;
    animation: ldio 1s cubic-bezier(0,0.2,0.8,1) infinite;

    &:nth-child(1) {
        border-color: #fec02f;
        animation-delay: 0s; }

    &:nth-child(2) {
        border-color: #fec02f;
        animation-delay: -0.5s; } }

.loadingio-spinner-ripple {
    width: 200px;
    height: 200px;
    display: inline-block;
    overflow: hidden; }

.ldio {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0;
    div {
        box-sizing: content-box; } }
