@import url('https://fonts.googleapis.com/css2?family=Fira+Mono&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap');

$body-bg: $grey1;
$block-bg: $grey5;
$text-color: darken($white, 40%);

html, body {
    font-family: 'Open Sans', sans-serif;
    background: $body-bg;
    color: $white;
    @include fs(15rem);
    @include media-breakpoint-up(xl) {
        overflow: hidden;
        cursor: none;
        * {
            cursor: none !important; } } }

html, body, .fullpage {
    @include media-breakpoint-up(lg) {
        height: 100%; }
    > .container {
        height: 100%;
        overflow: hidden; } }

.loading {
    background: $body-bg;
    @include flexbox;
    align-items: center;
    justify-content: center;
    @include pos(fix, 0px 0px 0px 0px);
    z-index: 9999; }

@mixin tailwind-boxshadow {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 25px 50px -12px; }

@mixin scrollbar {
    &::-webkit-scrollbar {
        width: 16px;
        background-color: $grey2;
        padding: 5px; }

    &::-webkit-scrollbar-track {
        -webkit-box-shadow: none;
        background-color: $grey2;
        @include bor-rad(100px); }

    &::-webkit-scrollbar-thumb {
        background-color: $grey4;
        border: 6px solid $grey2;
        @include bor-rad(100px); } }

.main-container {
    padding: 15px 0;
    @include media-breakpoint-up(lg) {
        padding: 0;
        height: 100%;
        @include flexbox;
        align-items: center;
        align-content: center;
        justify-content: center; } }

.main-block {
    @include media-breakpoint-up(lg) {
        @include flexbox; }

    .menu-container {
        @include pos(fix, null 15px 15px 15px);
        z-index: 1000;
        @include media-breakpoint-up(sm) {
            left: 50%;
            right: auto;
            width: 100%;
            max-width: 510px;
            @include transform(translateX(-50%)); }
        @include media-breakpoint-up(md) {
            max-width: 690px; }
        @include media-breakpoint-up(lg) {
            max-width: 60px;
            @include pos(static,null);
            @include transform(translateX(0%));
            margin-right: 15px; }
        .menu {
            background: color-rgba($white,0.1);
            backdrop-filter: blur(15px) brightness(70%);
            -webkit-backdrop-filter: blur(10px);
            @include bor-rad(10px);
            overflow: hidden;
            @include pos(rel,null);
            @include tailwind-boxshadow;
            @include media-breakpoint-up(lg) {
                background: $grey2;
                height: 100%; }
            ul {
                @include flexbox;
                align-items: center;
                align-content: center;
                justify-content: space-between;
                padding: 0px 15px;
                @include media-breakpoint-up(lg) {
                    padding: 0;
                    flex-direction: column;
                    height: 100%;
                    justify-content: center; }
                li {
                    flex: 1 1 25%;
                    @include media-breakpoint-up(lg) {
                        flex: unset;
                        width: 100%; }
                    a {
                        @include flexbox;
                        align-items: center;
                        align-content: center;
                        justify-content: center;
                        height: 60px;
                        @include fs(24rem);
                        color: color-rgba($white,0.5);
                        @include pos(rel,null);
                        @include transition(all,.5s);
                        &::after {
                            content: '';
                            display: block;
                            background: $yellow;
                            width: 100%;
                            height: 2px;
                            visibility: hidden;
                            opacity: 0;
                            @include pos(abs, null null 0 0);
                            @include transition(all,.5s);
                            @include media-breakpoint-up(lg) {
                                width: 2px;
                                height: 100%;
                                @include pos(abs, 0 null auto 0); } }
                        &.active {
                            color: $yellow;
                            @include media-breakpoint-up(lg) {
                                background: $grey4; }
                            &::after {
                                visibility: visible;
                                opacity: 1; } } } } }
            #menu-indicator {
                display: block;
                background: $yellow;
                height: 2px;
                @include pos(abs, null null 0 0);
                @include transition(all,0.5s,ease); } } }

    .content-container {
        margin-top: -15px;
        height: calc(100% - 45px);
        @include pos(rel,null);
        @include media-breakpoint-up(lg) {
            height: 80vh;
            max-height: 550px;
            width: calc(100% - 75px);
            background: $grey2;
            margin: 0;
            @include flexbox;
            @include bor-rad(10px);
            overflow: hidden; }
        @include media-breakpoint-up(xl) {
            max-height: 600px; }
        @include media-breakpoint-up(xxl) {
            max-height: 800px; } } }

.profile {
    margin-top: 15px;
    @include pos(rel,null);
    @include tailwind-boxshadow;
    @include media-breakpoint-up(lg) {
        margin-top: 0;
        flex: 3; }
    .img {
        height: 70vh;
        @include bor-rad(10px);
        overflow: hidden;
        background: $grey3;
        @include pos(rel,null);
        @include media-breakpoint-up(lg) {
            height: 100%;
            @include bor-rad(0px); }
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 50%;
            @include pos(abs, null 0 0 0);
            z-index: 3;
            background: linear-gradient(to top, $block-bg, color-rgba($block-bg, 0)); }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            @include bor-rad(10px);
            @include media-breakpoint-up(lg) {
                @include bor-rad(0); } } }
    .caption {
        @include pos(abs, null 0 0 0);
        padding-bottom: 30px;
        z-index: 3;
        text-align: center;
        margin-top: 15px;
        .name {
            @include fs(38rem);
            font-weight: 800;
            color: $white; }
        .title {
            @include fs(16rem);
            color: $yellow;
            font-weight: 700;
            text-transform: uppercase; }
        .btn-wrap {
            @include media-breakpoint-up(lg) {
                display: none; } } } }

@keyframes typing-effect {
    0% {
        border-color: color-rgba($white,0); }
    100% {
        border-color: color-rgba($white,1); } }

.typing-text {
    line-height: 1.2;
    margin: 15px 0 25px;
    padding: 0px 15px;
    @include fs(14rem);
    @include media-breakpoint-up(lg) {
 }        // +fs(12rem)
    .text-yellow {
        color: $yellow; }
    .text-code {
        border-right: 1px solid;
        font-family: 'Fira Mono', monospace;
        animation: typing-effect 1s infinite; } }

.scroll-content {
    @include media-breakpoint-up(lg) {
        flex: 5;
        overflow: auto;
        padding: 0px 30px;
        @include scrollbar; } }

.headline {
    @include fs(32rem);
    color: $white;
    font-weight: 800;
    margin-bottom: 20px; }

.content-section {
    padding-top: 50px;
    &:last-child {
        margin-bottom: 120px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 0; } } }
#about {
    margin-top: 55px;
    @include media-breakpoint-up(lg) {
        margin-top: 0; } }
#contact {
    min-height: 70vh;
    @include media-breakpoint-up(lg) {
        min-height: 0px;
        padding-bottom: 60px; }
    @include media-breakpoint-up(xl); }

.content {
    line-height: 1.7;
    color: $text-color;
    margin-bottom: 30px;
    &:last-child {
        margin-bottom: 0; }
    p {
        margin-bottom: 15px;
        &:last-child {
            margin-bottom: 0; } }

    a {
        color: $white;
        &:hover {
            color: $yellow; } }

    .text-white {
        color: $white; } }

.social {
    @include flexbox;
    align-items: center;
    align-content: center;
    a {
        @include flexbox;
        align-items: center;
        align-content: center;
        color: $text-color;
        @include fs(14rem);
        font-family: 'Fira Mono', monospace;
        text-transform: uppercase;
        + a {
            margin-left: 30px; }
        .fab {
            @include flexbox;
            width: 32px;
            height: 32px;
            align-items: center;
            align-content: center;
            justify-content: center;
            @include fs(18rem);
            color: $main;
            background: $yellow;
            @include bor-rad(100px);
            margin-right: 10px; }
        &:hover {
            color: $white;
            .fab {
                background: $white;
                &.fa-facebook-f {
                    color: #4267B2; }
                &.fa-linkedin-in {
                    color: #2867B2; } } } }

    // .fa-facebook
    // .fa-linkedin-in
 }    //     color: #2867B2

.skills {
    @include flexbox;
    .item {
        @include flexbox;
        align-items: center;
        align-content: center;
        justify-content: center;
        flex-direction: column;
        background: $grey5;
        color: $white;
        flex: 1;
        height: 100px;
        @include tailwind-boxshadow;
        @include bor-rad(10px);
        + .item {
            margin-left: 30px; }
        .fab {
            @include fs(32rem);
            margin-bottom: 5px;
            &.fa-angular {
                color: #dd0031; }
            &.fa-react {
                color: #61dafb; } }
        span {
            font-weight: 600; }
        // &:hover
        //     color: $main
 } }        //     background: $white

@keyframes bubble {
    0% {
        transform: translate(-50%, -50%) scale3d(1,1,1);
        opacity: 0; }
    30% {
        opacity: 0.3; }
    100% {
        transform: translate(-50%, -50%) scale3d(2.5,2.5,2.5);
        opacity: 0; } }

.timeline {
    padding-left: 30px;
    @include pos(rel,null);
    &::before {
        content: '';
        display: block;
        width: 4px;
        height: 100%;
        background: $grey3;
        @include pos(abs, 0 null null 10px); }
    .milestone {
        margin-bottom: 20px;
        @include pos(rel,null);
        &:last-child {
            margin-bottom: 0; }
        &::before {
            content: '';
            display: block;
            width: 10px;
            height: 10px;
            @include bor-rad(100px);
            background: $yellow;
            @include pos(abs, 11px null null -18px);
            @include transform(translate(-50%, -50%)); }
        &::after {
            content: '';
            display: block;
            width: 10px;
            height: 10px;
            @include bor-rad(100px);
            background: $yellow;
            opacity: 0.5;
            @include pos(abs, 11px null null -18px);
            transform: translate(-50%, -50%) scale3d(1.1,1.1,1.1);
            animation: bubble 1.5s infinite; }
        time {
            @include fs(14rem);
            font-weight: 600;
            display: block;
            color: $yellow;
            margin: 0; }
        .company {
            @include fs(24rem);
            line-height: 1.3;
            color: $white;
            font-weight: 700;
            margin: 0; }
        .title {
            font-weight: 700;
            text-transform: uppercase;
            margin: 0 0 10px; }
        ul {
            list-style-type: disc;
            padding-left: 25px; } } }

.education {
    background: $grey3;
    @include bor-rad(10px);
    padding: 15px;
    @include tailwind-boxshadow;
    @include flexbox;
    align-items: center;
    align-content: center;
    .fas {
        @include flexbox;
        align-items: center;
        align-content: center;
        justify-content: center;
        width: 64px;
        height: 64px;
        @include bor-rad(100px);
        @include fs(32rem); }
    .description {
        // flex: 1
        // padding-left: 15px
        time {
            @include fs(14rem);
            font-weight: 600;
            display: block;
            color: $yellow;
            margin: 0; }
        .university {
            @include fs(20rem);
            line-height: 1.5;
            color: $white;
            font-weight: 700;
            margin: 0 0 5px; }
        .title {
            @include fs(14rem);
            line-height: 1.5;
            text-transform: uppercase; } } }

.map {
    @include bor-rad(10px);
    overflow: hidden;
    @include tailwind-boxshadow;
    margin-bottom: 30px;
    iframe {
        display: block;
        margin: 0;
        @include media-breakpoint-up(md) {
            height: 350px; }
        @include media-breakpoint-up(lg) {
            height: 250px; }
        @include media-breakpoint-up(xxl) {
            height: 400px; } } }

.address {
    color: $text-color;
    @include media-breakpoint-up(lg) {
        margin-bottom: 50px; }
    ul {
        li {
            @include pos(rel,null);
            padding-left: 40px;
            margin-bottom: 20px;
            @include fs(16rem);
            @include media-breakpoint-up(lg) {
                margin-bottom: 10px; } }
        .fas, .fab {
            @include pos(abs, 5px null null 0);
            color: $yellow;
            @include fs(20rem);
            width: 36px;
            text-align: center;
            @include media-breakpoint-up(lg) {
                @include fs(18rem); } }
        a {
            color: $text-color; } } }
