// Text overlfow
@mixin textover($line) {
	display: -webkit-box;
	-webkit-line-clamp: $line;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis; }
@mixin textover2($fontSize,$lineHeight,$line) {
	display: -webkit-box;
	-webkit-line-clamp: $line;
	-webkit-box-orient: vertical;
	height: ($fontSize*$lineHeight*$line);
	overflow: hidden;
	text-overflow: ellipsis; }

// disabled scroll bar
@mixin disabledscrollbar {
	&::-webkit-scrollbar-track {
		display: none;
		background-color: transparent; }
	&::-webkit-scrollbar {
		display: none;
		width: 0;
		background-color: transparent; }
	&::-webkit-scrollbar-thumb {
		display: none;
		background-color: transparent;
		border: none; } }

// Font size px to rem
@mixin fs($size) {
	font-size: $size/16; }

// mini scrollbar
@mixin scrollbar() {
	&::-webkit-scrollbar-track {
		background-color: transparent;
		padding: 2px; }
	&::-webkit-scrollbar {
		width: 5px;
		background-color: transparent; }
	&::-webkit-scrollbar-thumb {
		background-color: color-rgba($black,0.3); } }
