.cursor {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    pointer-events: none;
    @include media-breakpoint-up(xl) {
        display: block; }
    &--small {
        width: 5px;
        height: 5px;
        left: -2.5px;
        top: -2.5px;
        border-radius: 50%;
        z-index: 11000;
        background: $yellow; }
    &--canvas {
        width: 100vw;
        height: 100vh;
        z-index: 12000; } }
