.mt-15 {
    margin-top: 15px; }

.btn-wrap {
    @include flexbox;
    justify-content: center; }
.btn {
    display: block;
    color: $white;
    background: $grey5;
    font-weight: 600;
    @include bor-rad(5px);
    padding: 10px 30px;
    text-transform: uppercase;
    &.btn-primary {
        color: $grey1;
        background: $yellow;
        &:hover, &:active {
            background: darken($yellow, 10%); } } }
