///
/// Margin settings.
///

@mixin mar($values...) {
  @include bn-property(margin, false, $values...); }
@mixin margin($values...) {
  @include bn-property(margin, false, $values...); }

///
/// Padding settings.
///

@mixin pad($values...) {
  @include bn-property(padding, false, $values...); }
@mixin padding($values...) {
  @include bn-property(padding, false, $values...); }
